/* ===========================================================

	# Fade in animations

=========================================================== */
@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.fadeIn {
  animation-name: fadeIn; }

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translate3d(0, 2rem, 0); }
  to {
    opacity: 1;
    transform: none; } }

.fadeInUp {
  opacity: 1;
  transform: translate3d(0, 0, 0); }

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translate3d(5rem, 0, 0); }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0); } }

.fadeInRight {
  animation-name: fadeInRight; }

/* ===========================================================

	# Block reveal animations

=========================================================== */
.enhance .block-revealer__element {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000;
  pointer-events: none;
  opacity: 0; }

